@import url(https://fonts.googleapis.com/css?family=Oxygen Mono:400,500,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body {
  font-family:monospace;
  background-color: #fff;
  color: black;
  margin: 0;
  width: 100%;
  padding: 0;
}
* {
  box-sizing: border-box;
}


.col-1 {
  width: 8.33%;
}
.col-2 {
  width: 16.66%;
}
.col-3 {
  width: 25%;
}
.col-4 {
  width: 33.33%;
}
.col-5 {
  width: 41.66%;
}
.col-6 {
  width: 50%;
}
.col-7 {
  width: 58.33%;
}
.col-8 {
  width: 66.66%;
}
.col-9 {
  width: 75%;
}
.col-10 {
  width: 83.33%;
}
.col-11 {
  width: 91.66%;
}
.col-12 {
  width: 100%;
}

[class*="col-"] {
  float: left;
  padding: 20px;
  text-align: left;
}

.row {
  padding: 120px;
}

.row h1 {
  text-align: center;
  font-size: 40px;
}

.row::after {
  content: "";
  clear: both;
  display: table;
}
nav {
  position: fixed;
  width: 100%;
  line-height: 60px;
  z-index: 9999;
}

nav ul {
  line-height: 60px;
  list-style: none;
  background:#fff;
  overflow: hidden;
  color: #000;
  padding: 0;
  text-align: right;
  margin: 0;
  padding-right: 40px;
  transition: 1s;
}



nav ul li {
  display: inline-block;
  padding: 16px 40px;
  
}

nav ul li a {
  text-decoration: none;
  color: #040a26;
  font-size: 30px;
  font-weight: bold;
  letter-spacing: 3px;
}

nav ul li a:hover {
  text-shadow: 5px 5px 10px #040a26;

}

.menu-icon {
  line-height: 60px;
  width: 100%;
  background: #000;
  text-align: right;
  box-sizing: border-box;
  padding: 15px 24px;
  cursor: pointer;
  color: #fff;
  display: none;
}

.logo a {
  text-decoration: none;
  line-height: 60px;
  position: fixed;
  float: left;
  margin: 16px 46px;
  color: #040a26;
  font-weight: bold;
  font-size: 60px;
  letter-spacing: 3px;
}

.logo a:hover {
  text-shadow: 5px 5px 10px #040a26;

}


.active
{
  font-weight: bolder;
}

.hero-text {
  font-size: 50px;
  font-family: monospace;
  font-weight: bold;
  text-align: left;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #040a26;
  letter-spacing: 3px;
}

.line {
  height: 200px;
  position: fixed;
  left: 0.1%;
  margin-left: -3px;
  bottom: 5%;
}

.vl {
  border-left: 6px solid #040a26;
  height: 220px;
  position: fixed;
  left: 1%;
  margin-left: -3px;
  bottom: 0%;
}

.line ul {
  list-style-type: none;
}

hr {
  display: block;
  margin-left: 120px;
  margin-right: 120px;
}

.fa {
  color: #040a26;
  font-size: 36px;
  padding: 10px;
}
.fa:hover {
  text-shadow: 5px 5px 10px #040a26;
  -webkit-animation: shake 0.5s;
          animation: shake 0.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
#main {
  height: 100vh;
  box-shadow: 0 36px 46px 0 #040a26;
  margin-left: 100px;
  margin-right: 100px;  
}

#about {
  background: #040a26;
  color: #fff;
  box-shadow: 0 36px 46px 0 #040a26;
  margin-left: 100px;
  margin-right: 100px;
}

ul.a {
  list-style-type: circle;
  -moz-column-count: 2;
  -moz-column-gap: 20px;
  -webkit-column-count: 2;
  -webkit-column-gap: 20px;
  column-count: 2;
  grid-column-gap: 20px;
  column-gap: 20px;
}

.info {
  text-align: center;
  margin-top: 40px;
}
.info a i{
  color: aqua;
}

.prof-img {
  position: relative;
  width: 200px;
  border-radius: 8px;
  box-shadow: 5px 10px 18px #040a26;
}

.prof-img:hover {
  box-shadow: 5px 10px 18px aqua;
  bottom: -15px;
}

.profilepic {
  display: block;
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.overlay {
  position: absolute;
  bottom: 0;
  background: rgb(0, 0, 0);
  background: rgba(0, 0, 0, 0.5);
  color: #f1f1f1;
  width: 100%;
  transition: .5s ease;
  opacity: 0;
  color: white;
  font-size: 20px;
  padding: 20px;
  text-align: center;
}

.prof-img:hover .overlay {
  opacity: 1;
}

#projects {
  background: #fff;
color: #000;
box-shadow: 0 36px 46px 0 #040a26;
margin-left: 100px;
margin-right: 100px;
}

.card {
box-shadow: 0 26px 26px 0 rgba(1, 1, 1, 1);
text-align: center;
transition: 0.1s;
width: 100%;
height: 250px;
background: #040a26;
margin-bottom: 20px;
border-style: solid;
border-color: #040a26;
border-radius: 10px;
}
.card a i{
  float: right;
  color: aqua;
}

.card-container {
padding-top: 50px;
color: #fff;
}

.card-container a {
color: #fff;
text-decoration: none;

}
.card-container ul {
  -webkit-column-count: 1;
          column-count: 1; 
  list-style-type: none; 
  display: inline;
}

.card:hover {
box-shadow: 5px 10px 18px #040a26;
position: relative;
right: -15px;
transition: top ease 0.5s;
}

#photography {
background-color: #040a26;
box-shadow: 0 16px 46px 0 #040a26;
margin-left: 100px;
margin-right: 100px;
}
#photography h1{
  color: #fff;
}

.photo-column {
width: 100%;
}

.photo-column {
display: flex;
flex-wrap: wrap;
padding: 0 4px;
}

.column {
flex: 25% 1;
max-width: 25%;
padding: 0 4px;
}

.column img {
margin-top: 8px;
vertical-align: middle;
width: 100%;
border-radius: 8px;
}

.column img:hover {
box-shadow: 5px 10px 18px #fff;
}
footer a i:hover, .info a i:hover, #line ul li i:hover {
  -webkit-animation: shake 0.5s;
          animation: shake 0.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes shake {
  0% {
      transform: translate(1px, 1px) rotate(0deg);
  }

  10% {
      transform: translate(-1px, -2px) rotate(-1deg);
  }

  20% {
      transform: translate(-3px, 0px) rotate(1deg);
  }

  30% {
      transform: translate(3px, 2px) rotate(0deg);
  }

  40% {
      transform: translate(1px, -1px) rotate(1deg);
  }

  50% {
      transform: translate(-1px, 2px) rotate(-1deg);
  }

  60% {
      transform: translate(-3px, 1px) rotate(0deg);
  }

  70% {
      transform: translate(3px, 1px) rotate(-1deg);
  }

  80% {
      transform: translate(-1px, -1px) rotate(1deg);
  }

  90% {
      transform: translate(1px, 2px) rotate(0deg);
  }

  100% {
      transform: translate(1px, -2px) rotate(-1deg);
  }
}

@keyframes shake {
  0% {
      transform: translate(1px, 1px) rotate(0deg);
  }

  10% {
      transform: translate(-1px, -2px) rotate(-1deg);
  }

  20% {
      transform: translate(-3px, 0px) rotate(1deg);
  }

  30% {
      transform: translate(3px, 2px) rotate(0deg);
  }

  40% {
      transform: translate(1px, -1px) rotate(1deg);
  }

  50% {
      transform: translate(-1px, 2px) rotate(-1deg);
  }

  60% {
      transform: translate(-3px, 1px) rotate(0deg);
  }

  70% {
      transform: translate(3px, 1px) rotate(-1deg);
  }

  80% {
      transform: translate(-1px, -1px) rotate(1deg);
  }

  90% {
      transform: translate(1px, 2px) rotate(0deg);
  }

  100% {
      transform: translate(1px, -2px) rotate(-1deg);
  }
}

@media only screen and (max-width: 486px) {
  ul.a {
      -webkit-column-count: 1;
              column-count: 1;
  }
}

@media only screen and (max-width:586px){
  #main, #about, #projects, #photography {
      margin-left: 20px;
      margin-right: 20px; 
  }
}

@media only screen and (max-width: 786px) {

  .hero-text {
      font-size: 30px;
      text-align: center;
      top: 50%;
      left: 50%;
  }

  .row {
      padding-left: 10px;
      padding-right: 10px;
  }

  .row h1 {
      font-size: 30px;
  }

  [class*="col-"] {
      width: 100%;
      padding: 20px;
  }

  hr {
      margin: 0px;
  }


}

@media screen and (max-width: 800px) {
  .vl,.line {
          display: none;
      }

  .column {
      flex: 100% 1;
      max-width: 100%;
  }
}

@media only screen and (max-width: 1000px) {

  .logo {
      position: fixed;
      top: 0;
      display: block;
  }

  [class*="col-"] {
      width: 100%;
      padding: 20px;
  }

  .hero-text {
      text-align: center;
      top: 50%;
      left: 50%;
  }
  .prof-img {
      display: block;
      margin-left: auto;
      margin-right: auto;
  }

  nav ul {
      max-height: 0px;
      background: #fff;
  }

  nav.black ul {
      background: #fff;
  }

  .showing {
      max-height: 34em;
  }

  nav ul li {
      box-sizing: border-box;
      width: 100%;
      padding: 24px;
      text-align: center;
  }

  nav ul li a:active {
      color: #040a26;
  }

  .menu-icon {
      display: block;
      background: #fff;
  }

  footer a i:hover,
  .info a i:hover,
  .line ul li i:hover, #myBtn:hover, .fa:hover {
      -webkit-animation: shake 0.0s;
              animation: shake 0.0s;
  }
}


